import React, { useState } from 'react';
import axios from 'axios';

import { ThemeProvider, makeStyles } from '@material-ui/core/styles/';
import {Button, Grid, LinearProgress, withStyles, TextField, Container} from '@material-ui/core';

/* Components*/

/* Styles */
import theme from '../Styles/Theme';

const styles = theme => ({
    Container_main:{
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2),
    },
    Container_column:{
        border: '1px solid black',
        maxWidth: 600,
        height: 337,
    },
    Button_upload:{
        display: 'block',
        margin: '0 auto'
    },
    Images:{
        width: '100%',
        height: '100%'
    }
});


class Comparator extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            originalImage : '',
            previewOriginalImage: '',
            loadingImage : ''
          };
    }

    uploadImage = e => {
        console.log(e.target.files[0])
        this.setState({
            originalImage : e.target.files[0],
            previewOriginalImage : URL.createObjectURL(e.target.files[0])
        })
        this.sendImages()

    }

    handleLoadImage = e => {
        e.preventDefault();
        document.getElementById('Image_input').click()
    }

    sendImages = async () =>{
        
        let formData = new FormData();
        formData.append('imagen_sin_analizar', this.state.originalImage);


        try {
            const images = await axios({
                method: 'post',
                url: `http://13.59.89.50/v1/images`,
                data: formData,
            })
            console.log(images.data)
        } catch (error) {
            console.log(error)
        }
        /*
        await axios({
              method: 'post',
              url: 'http://13.59.89.50/v1/images',
              data: formData,
              headers: {'Content-Type': 'multipart/form-data' }
          }).then(response => (
            console.log(response)
            //setLoadingImage(false)
          )).catch(error =>(
            console.log(error)
          ))
            */
        } 

    render(){
        const { classes } = this.props;


        return(
            <ThemeProvider theme={theme}>
                <Button color="primary"
                        variant="contained"
                        className={classes.Button_upload}
                        onClick={this.handleLoadImage}
                >
                    Subir Imagen
                </Button>
                <input type="file" id="Image_input" accept="image/*" style={{display:'none'}} onChange={this.uploadImage}/>
                <Grid container className={classes.Container_main} spacing={4}>
                    <Grid item xs={12} sm={12} md={6}>
                        <div className={classes.Container_column}>
                            {
                                this.state.previewOriginalImage
                                ? <img src={ this.state.previewOriginalImage } className={classes.Images} alt="preview" /> 
                                : null
                                
                            }
                        </div>
                    </Grid>
                    <Grid item xs={12} sm={12} md={6} >
                        <div className={classes.Container_column}>
                            {/*
                                loadingImage
                                ? <LinearProgress color="primary" />
                                : null
                            */}
                        </div>
                    </Grid>
                </Grid>
                <Container maxWidth="sm">
                    <Grid container className={classes.Container_main} spacing={4}>
                        <Grid item xs={12} sm={12} md={4}>
                            <TextField fullWidth id="Text_CajasTeoricas" label="Vehiculos teoricos" />
                        </Grid>
                        <Grid item xs={12} sm={12} md={4}>
                            <TextField fullWidth id="Text_CajasDetectadas" label="Veh√≠culos detectados" />
                        </Grid>
                        <Grid item xs={12} sm={12} md={4}>
                            <TextField
                                id="standard-read-only-input"
                                label="Porcentaje"
                                disabled
                                fullWidth
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <Button color="primary"
                                    variant="contained"
                                    className={classes.Button_upload}
                                    
                            >
                                Enviar
                            </Button>
                        </Grid>
                    </Grid>
                </Container>
            </ThemeProvider>
        )
    }


}

export default withStyles(styles)(Comparator);

